import Request from '@/js_sdk/luch-request/luch-request/index.js'
import context from '@/main.js'

const testApiUrl = 'https://test.api.mokedao.com/'
const productApiUrl = 'https://api.mokedao.com/'

const commonUUID = "MOKEDAO_H5_XIESHENG"

const http = new Request()

var uuid, version, identifier
























	uuid = commonUUID
	identifier = "xs_h5"
	version = "1.0"


http.setConfig((config) => { /*  */
		config.baseUrl = productApiUrl
		config.header = {
			"Accept-Language": "zh",
			"x-client-appkey": "1",
			"x-client-udid": uuid,
			"x-client-identifier": identifier,
			"x-client-ver": version,
			"x-client-CID": ""
		}
		return config
	})

http.interceptors.request.use((config) => { /*  */
	config.header = {
		...config.header
	}
	/**
		 * custom {Object} - 
		 */
	// if (config.custom.auth) {
	//   config.header.token = '123456'
	// }
	// if (config.custom.loading) {
	//   uni.showLoading()
	// }
	/*
	if (!token) { // tokenreturn Promise.reject(config) 
	  return Promise.reject(config)
	}
	*/
	return config
}, (config) => {
  return Promise.reject(config)
})

http.interceptors.response.use((response) => { /*  */
  // if (response.config.custom.loading) {
  //    uni.hideLoading()
  //  }
  if (response.data.code !== 200) { // 200reject()
    return Promise.reject(response)
  }

	if(response.data != null && typeof(response.data) == 'string') {
		response.data = JSON.parse(response.data.trim())
	}
	console.log(response)
	if(response.data.status === 0) {
		if(response.data.code === 10002) {
			uni.showModal({
				content:'',
				showCancel:false
			})
		} else if(response.data.code === 20105) {  // session
			uni.showModal({
				content:'',
				showCancel:false,
				complete:function(){
					uni.navigateTo({
						url:'/pages/login/login'
					})
				}
			})
		}
	}
	return response
}, (response) => { // 
	context.$toast.showError()
	return Promise.reject(response)
})

export {
	http
}