
function showLoading(message) {
	message = message || ""
	uni.showLoading({
		title:message
	})
}

function hideLoading() {
	uni.hideLoading();
}

function showSuccess(message) {
	message = message || ''
	uni.showToast({
		title:message,
		icon:'success'
	})
}

function showToast(message) {
	uni.showToast({
		title:message,
		icon:'none'
	})
}

function showError(message) {
	message = message || ''
	uni.showToast({
		title:message,
		icon:'none'
	})
}

module.exports = {
	showLoading: showLoading,
	hideLoading: hideLoading,
	showSuccess: showSuccess,
	showError: showError,
	showToast: showToast
}